.thumb {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 340px;
  border: 1px solid rgba(1, 62, 65, 0.288);
  padding: 20px;
  text-align: center;
}
.thumb:hover {
  box-shadow: 10px 5px 5px rgba(0, 139, 139, 0.11);
}

.name {
  font-size: 16px;
  font-weight: bold;
  margin-right: 15px;
}

.input {
  width: 220px;
  margin-bottom: 25px;
  padding: 5px;
  border: 1px solid rgba(1, 62, 65, 0.288);
  outline: none;
}
.input:focus {
  border: 3px solid rgba(0, 139, 139, 0.445);
}

.button {
  display: block;
  width: 150px;
  border: 1px solid rgba(128, 128, 128, 0.438);
  border-radius: 3px;
  margin-top: 25px;
}
.button:hover,
.button:focus {
  background-color: rgba(0, 139, 139, 0.445);
  color: bisque;
}
