.thumb {
  display: flex;
  flex-direction: column;
  width: 340px;
  text-align: center;
}

.contactList {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  margin-bottom: 10px;
  list-style: decimal;
}

.button {
  width: 50px;
  border: 1px solid rgba(128, 128, 128, 0.438);
  border-radius: 3px;
  margin-left: 15px;
  color: rgba(139, 0, 35, 0.918);
}
.button:hover,
.button:focus {
  background-color: rgba(139, 0, 35, 0.445);
  color: bisque;
}
